import React from "react";
import styled from 'styled-components';

import {List, Button} from 'antd';
import {DeleteTwoTone} from '@ant-design/icons';

const StyledList = styled(List)`
    padding: 16px;
`;

const DeadlineList = ({data, onDelete}) => {
    const formatDate = date => `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
    const formatDescription = item => `${formatDate(new Date(item.start))} ~ ${formatDate(new Date(item.end))}`;

    return (
        <StyledList
            itemLayout="horizontal"
            dataSource={data}
            renderItem={(item, index) => (
                <List.Item
                    actions={[<Button type="primary" shape="circle" icon={<DeleteTwoTone twoToneColor="#eb2f96"/>} onClick={() => onDelete(item.id, index)}/>]}>
                    <List.Item.Meta
                        title={item.title}
                        description={formatDescription(item)}
                    />
                </List.Item>
            )}
        />
    );
}

export default DeadlineList;